import React from 'react';
import { Link } from 'react-router-dom';
import { AdminView } from '../views';

export default function SettingsIndexView(props) {
  const { path } = props.match;

  return (
    <AdminView title="Settings" className="settings" back={props.back}>
      <AdminView.Section title="Website">
        <div className="d-grid grid-sm-3 grid-md-4">
          <div className="">
            <SettingsLink to={`${path}general/`} title="General" text="Site settings and branding" />
          </div>

          <div className="">
            <SettingsLink to={`${path}index/`} title="Index" text="Homepage settings" />
          </div>

          <div className="">
            <SettingsLink to={`${path}nav/`} title="Navigation" text="Site navigation" />
          </div>
        </div>
      </AdminView.Section>
    </AdminView>
  );
}

const SettingsLink = (props) => {
  const { title, text, ...rest } = props;
  return (
    <Link {...rest}>
      <div>
        <span className="text-md">{title}</span>
        <p className="text-muted text-sm pb-3">{text}</p>
      </div>
    </Link>
  );
};
